import React, { useState } from 'react'

export default function TextForm(props) {

    const handleUpClick = () => {
        let newText = text.toUpperCase();
        setText(newText);
        props.showAlert("Converted to UPPERCASE.","success")
    }

    const handleLoClick = () => {    
        let newText = text.toLowerCase();
        setText(newText);
        props.showAlert("Converted to lowercase.","success")
    }

    const handleSpacesClick = () => {
        let newtext = text.split(/[ ]+/);
        setText(newtext.join(" "));
        props.showAlert("Spaces are removed.","success")
    };

    const handleClearClick = () => {
        let newText = "";
        setText(newText);
        props.showAlert("Text Cleared.","success")
    }

    const handleCopyClick = () => {
        let text = document.getElementById("myBox");
        text.select();
        navigator.clipboard.writeText(text.value);
        // document.getSelection().removeAllRanges();
        props.showAlert("Copied to the Clipboard.","success")
    }

    const handleOnChange = (event) => {
        console.log("onchange was clicked");
        setText(event.target.value);
    }

    const [text, setText] = useState('enter text here...');



    //text = 'new text'; //wrong way to change the state
    //setText("new text"); //correc way to change the state

    return (
        <>
            <div className="container" style={{ color: props.uiMode === 'dark' ? 'white' : 'black' }}>
                <h1 style={{fontFamily: 'nunito'}}>{props.heading}</h1>

                <div className="mb-3 mx-2">
                    <textarea className="form-control" value={text} onChange={handleOnChange} id="myBox" rows="5" 
                    style={{
                        backgroundColor: props.uiMode === 'dark' ? '#30475E' : '#e7e7e7',
                        color: props.uiMode === 'dark' ? 'white' : 'black',
                        border: 'none',
                        borderRadius: '20px',
                        padding: '5px 20px',
                    }}>
                    </textarea>
                </div>

                <button disabled={text.length===0} className="btn btn-primary mx-1 my-1" onClick={handleUpClick}  > Convert to UpperCase</button>
                <button disabled={text.length===0} className="btn btn-primary mx-1 my-1" onClick={handleLoClick}  > Convert to LowerCase</button>
                <button disabled={text.length===0} className="btn btn-primary mx-1 my-1" onClick={handleSpacesClick}  > Trim Spaces</button>
                <button disabled={text.length===0} className="btn btn-primary mx-1 my-1" onClick={handleCopyClick}  > Copy Text</button>
                <button disabled={text.length===0} className="btn btn-secondary mx-1 my-1" onClick={handleClearClick}  > Clear</button>

            </div>
            <div className="container my-1" style={{ color: props.uiMode === 'dark' ? 'white' : 'black' }}>
                <h2 style={{fontFamily: 'nunito'}}>your text summary</h2>
                <div className="container mx-2" style={{backgroundColor: props.uiMode === 'dark' ? '#30475E' : '#e7e7e7',border: 'none', borderRadius: '20px', padding: '5px 11px',}}>

                <p className="mx-2">{text.split(" ").filter((element) =>{return element.length!==0}).length} words and {text.length} characters</p>

                <p className="mx-2">{0.008 * text.split(" ").filter((element) =>{return element.length!==0}).length} minutes to read</p>
                </div>

                <h2 style={{fontFamily: 'nunito'}}> preview</h2>

                <p className="mx-2" style={{backgroundColor: props.uiMode === 'dark' ? '#30475E' : '#e7e7e7',border: 'none', borderRadius: '20px', padding: '5px 20px',}}> {text.length > 0 ? text : "Nothing to preview!"}</p>

            </div>
        </>
    )
}
