import React from 'react'
import PropTypes from 'prop-types'
// import { Link } from 'react-router-dom'


export default function Navbar(props) {
	return (
		<nav className={`navbar navbar-expand-lg navbar-${props.uiMode} bg-${props.uiMode}`}>
			<div className="container-fluid">

				{/* Title Navigation Item  */}
				<a className="navbar-brand" href="#"><b>{props.title}</b></a>
				<button className="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
					<span className="navbar-toggler-icon"></span>
				</button>
				<div className="collapse navbar-collapse" id="navbarSupportedContent">
					<ul className="navbar-nav me-auto mb-2 mb-lg-0">
						<li className="nav-item">
							{/* Home Navigation Item  */}
							{/* <Link className="nav-link active" aria-current="page" to="/"><b>Home</b></Link> */}
							<a className="nav-link active" aria-current="page" href="#"><b>Home</b></a>
						</li>
						<li className="nav-item">
							{/* About Navigation Item */}
							{/* <Link className="nav-link" to="/about"><b>{props.aboutText}</b></Link> */}
						</li>
					</ul>
					<div className={`form-check form-switch text-${props.uiMode === 'light' ? 'dark' : 'light'}`}>
						<input className="form-check-input" onClick={props.toggleUIMode}  type="checkbox" id="flexSwitchCheckDefault" />
						<label className="form-check-label" htmlFor="flexSwitchCheckDefault">Dark Mode</label>
					</div>

				</div>
			</div>
		</nav>
	)
}


Navbar.propTypes = {
	title: PropTypes.string.isRequired,
	aboutText: PropTypes.string
}

Navbar.defaultProps = {
	title: 'set the title here',
	aboutText: 'set about here'
}